import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import _ from 'lodash';
import Panel from '../../components/organisms/Panel';
import FormField from '../../components/molecules/FormField';
import SelectField from '../../components/atoms/SelectField';
import TextField from '../../components/atoms/TextField';
import FormButtons from '../../components/molecules/FormButtons';
import {
  addIssue
} from '../../state/actions';

class ItemAdd extends Component {

  handleSubmit = (e) => {
    e.preventDefault();
    if (!e.target.checkValidity()) {
      e.stopPropagation();
      if (!e.target.className.match(/was-validated/)) {
        e.target.className += ' was-validated';
      }
      return;
    }
    const {
      description,
      severity,
      status
    } = e.target.elements;
    // translate to the correct form
    const issue = {
      description: description.value,
      severity: severity.value,
      status: status.value,
      created: new Date().toISOString(),
      resolved: status.value === 'closed' ? new Date().toISOString() : ''
    };
    this.props.addIssue(issue, () => {
      this.props.history.replace('/');
    });
  }

  render() {
    const { replace: replaceHistory } = this.props.history;
    return (
      <Panel
        title="Create a new claim"
        panelClass="bg-primary"
      >
        <form onSubmit={ this.handleSubmit } className="form-horizontal needs-validation" noValidate>
          <FormField labelText="Description">
            <TextField
              id="description"
              placeholder="Enter a description"
              required
            />
          </FormField>
          <FormField labelText="Severity">
            <SelectField
              id="severity"
              opts={[{
                value: 'minor',
                label: 'Minor'
              }, {
                value: 'major',
                label: 'Major'
              }, {
                value: 'critical',
                label: 'Critical'
              }]}
            />
          </FormField>
          <FormField labelText="Status">
            <SelectField
              id="status"
              opts={[{
                value: 'open',
                label: 'Open'
              }, {
                value: 'in progress',
                label: 'In Progress'
              }, {
                value: 'closed',
                label: 'Closed'
              }]}
            />
          </FormField>
          <FormButtons
            cancel={{
              label: 'Cancel',
              onClick: () => replaceHistory('/')
            }}
            submit={{
              label: 'Save',
              disabled: false
            }}
          />
        </form>
      </Panel>
    );
  }
}

ItemAdd.propTypes = {
  addIssue: PropTypes.func.isRequired
};

const mapStateToProps = state => state;

const mapDispatchToProps = dispatch => ({
  addIssue: (issue, callback) => addIssue(issue, callback, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ItemAdd);
